import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect } from 'preact/hooks';
import { InputLogic } from '../../input/inputlogic';
import ExpenseInput from '../../components/ExpenseInput';

const Food = () => {

	useEffect(() => {
		InputLogic.focusFirstInputElement()
	}, []);

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>

			<div class="container">
				<hr/>
				<h5>Food</h5>
				Not all the expenses here apply to your situation, only check on those item that is applicable. The expense amount can be estimates. Click next when you are done.<p/>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.food.groceries} title="Groceries/Supermarket" min_value="0" max_value="10000"
							description="Estimated grocery or supermarket expenses."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.food.restaurant} title="Restaurant" min_value="0" max_value="50000"
							description="Estimated expenses of eating out in restaurants."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.food.food_delivery} title="Food deliveries" min_value="0" max_value="20000"
							description="Estimated expenses of using food delivery service like Doordash, Uber eat etc."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.food.bakery_coffee} title="Coffee or Bakeries" min_value="0" max_value="20000"
							description="Estimated expenses related to coffee shop or bakeries."/>					
					</div>
				</div>	
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.food.bar_lounge} title="Bar / Lounge" min_value="0" max_value="5000"
							description="Estimated expenses related to bar or lounges."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.food.other} title="Other" min_value="0" max_value="20000"
							description="Other food/beverage cost."/>
					</div>
				</div>		
                <hr/>
				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/retailer')}>Back: Retailers</button>&nbsp;
				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/transport')}>Next: Transportation</button>
				<hr/>                						
			</div>
		</div>
		
	);
};

export default Food;
